@import url(https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,700;1,400&display=swap);
.small-header{color:#202C3E}

.cell-header{display:table-cell;padding:0.5em;border-bottom:1px solid #C0CCDA;color:#202C3E}.table-heading-container{display:flex;justify-content:space-between;min-width:10em}.table-heading{border-right:1px #C0CCDA solid}.table-heading:nth-last-of-type(1){border-right:none}.table-heading:hover{cursor:pointer}.sort-icon{color:#C0CCDA}

.cell-data{display:table-cell;text-align:center;padding:0.5em;border-bottom:1px solid #C0CCDA;height:100%}.container-chart{width:80%}.chart{height:0.8em;background-color:#FFBE82}

.table-row{display:table-row;color:#3D4659}.table-row:hover{cursor:pointer;border-left:1px solid #C0CCDA;box-shadow:3px 3px 3px #C0CCDA}

.container-movie-table{grid-area:movie-table;overflow:scroll;display:flex;flex-direction:column;align-items:center;padding:0 1em 1em 1em;background-color:#fff}.movie-table{display:table;border-collapse:collapse;position:relative}.movie-table-header{display:table-row;font-size:1.2em;position:-webkit-sticky;position:sticky}.button-show-more-movies{background-color:#5CC883;color:#fff;padding:0.5em 0;width:10em;font-size:1.5em;margin-top:1em;border:none;border-radius:5px}

.search{grid-area:searchbar;display:flex;justify-content:center;align-items:center;margin-bottom:1.5em}.search-input{width:20em;height:1.75em;font-size:1em;margin:0 0.25em;border:1px solid #C0CCDA;border-radius:5px}.search-button{border:none;border-radius:5px;margin:0 0.25em;padding:0 1em;font-size:1em;height:1.75em;background-color:#5CC883;color:#fff}

.detail-pane{background-color:#2E99E6;grid-area:detail-pane;padding:1em;display:flex;flex-direction:column;overflow:scroll;color:#E0E6ED}.button-close-pane{position:relative;width:2em;left:93%;bottom:1%;font-size:1em;border:none;border-radius:3px;background-color:#E0E6ED;color:#202C3E}

.poster{width:80%}

.detail-title{margin:0;font-size:1.5em}.detail-icon{margin:0px}.detail-list{list-style:none;padding:0}.detail-list li{margin:0.5em 0}

.large-header{margin:0;color:#202C3E}

header{padding:1em}footer{padding:1em;background-color:#1465cb;color:#fff}.default-layout{display:grid;height:100vh;grid-template-columns:auto;grid-template-rows:5% auto;grid-template-areas:"searchbar" "movie-table"}.expanded-layout{display:grid;height:100vh;grid-template-columns:30% auto;grid-template-rows:5% auto;grid-template-areas:"searchbar searchbar" "detail-pane movie-table"}

html,body{margin:0;height:100%;box-sizing:border-box}body{font-family:'Roboto', Arial, sans-serif;background-color:#95D8FF}button{cursor:pointer}a{color:#FFBE82}

